.quality {
  margin-bottom: 95px;

  &__wrapper {
    min-height: 436px;
    margin-bottom: 56px;
    position: relative;
    background-image: url('../img/quality/quality-bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-color: #2f2f51;
    background-blend-mode: multiply;
  }

  &__img {
    margin: 0 auto;
    margin-bottom: 55px;
    padding-top: 35px;
    display: block;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 70px;
    // justify-content: center;
    justify-content: center;
  }

  &__item {
    text-align: center;
    padding-top: 135px;
    position: relative;
    color: var(--light-color);

    &::before {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      background-size: cover;
      background-repeat: no-repeat;
    }

    &--1 {
      &::before {
        content: url('../img/quality/1.svg');
      }
    }

    &--2 {
      &::before {
        content: url('../img/quality/2.svg');
      }
    }

    &--3 {
      &::before {
        content: url('../img/quality/3.svg');
      }
    }

    &--4 {
      &::before {
        content: url('../img/quality/4.svg');
      }
    }

    &--5 {
      &::before {
        content: url('../img/quality/5.svg');
      }
    }
  }

  &__text {
    margin: 0;
  }

  &__btn {
    display: block;
    margin: 0 auto;
    font-size: 16px;
    padding-left: 76px;
    padding-right: 54px;

    &::before {
      left: 25px;
    }

    @include mobile {
      font-size: 14px;

      &::before {
        display: block;
      }
    }
  }
}
